html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

header {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0.5)),
    to(rgba(0, 0, 0, 0.5))),
    url(/wiegehtsdireuropa/static/media/hero_1050.2beecbf2.webp) no-repeat center center fixed;
  background: -webkit-linear-gradient(
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)),
    url(/wiegehtsdireuropa/static/media/hero_1050.2beecbf2.webp) no-repeat center center fixed;
  background: linear-gradient(
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)),
    url(/wiegehtsdireuropa/static/media/hero_1050.2beecbf2.webp) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
}

@media all and (min-width: 1050px) {
  header {
    background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.5)),
      to(rgba(0, 0, 0, 0.5))),
      url(/wiegehtsdireuropa/static/media/hero_1586.f8cf898b.webp);
    background-image: -webkit-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)),
      url(/wiegehtsdireuropa/static/media/hero_1586.f8cf898b.webp);
    background-image: linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)),
      url(/wiegehtsdireuropa/static/media/hero_1586.f8cf898b.webp)
  }
}

@media all and (min-width: 1586px) {
  header {
    background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.5)),
      to(rgba(0, 0, 0, 0.5))),
      url(/wiegehtsdireuropa/static/media/hero_1920.0f9dac4f.webp);
    background-image: -webkit-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)),
      url(/wiegehtsdireuropa/static/media/hero_1920.0f9dac4f.webp);
    background-image: linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)),
      url(/wiegehtsdireuropa/static/media/hero_1920.0f9dac4f.webp)
  }
}

.App-logo {
  position: absolute;
  top: 10%;
  left: 10%;
  -webkit-transform: translate(-10%, -50%);
          transform: translate(-10%, -50%);
}

.App-logo img{
  width: 100%;
  height: auto;
}

.App-text {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}

h1 {
  text-transform: uppercase;
  margin: 0;
  font-size: 3rem;
}

main {
  margin: 10px auto;
  font-size: 16px;
  
}
.ImageList {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 10px;
  grid-auto-flow: column;
  grid-auto-columns: calc(50% - 40px);
  overflow-x: scroll;
}

.ImageList::-webkit-scrollbar {
  display: none;
}

.ImageList::before,
.ImageList::after {
  content: '';
  width: 10px;
}
.Image-container {
  min-width: 75%;
  margin: 50px 15px 30px;
	padding: 10px 10px 50px;
  box-shadow: 0 4px 6px rgba(0,0,0,.3);
  font-size: 18px;
  font-weight: bold;
  -webkit-transform: none;
          transform: none;
}

.Image-container::before {
  content: url(/wiegehtsdireuropa/static/media/clothespin.1923b01b.svg);
  display: block;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  height: 117px;
  z-index: 2;
}

.Image-container:after {
  content: attr(title);
}

/* Large screens */
@media all and (min-width: 1200px) {
  .Image-container {
    min-width: 50%;
  }
}
 
/* Rotate all even images -2 degrees */
.Image-container:nth-child(2n) {
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

/* Rotate every fifth image by 2 degrees */
.Image-container:nth-child(3n) {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.duotone {
  position: relative;
}

.duotone::after {
  position: absolute;
  content: "\A9   Staatsarchiv Freiburg";
  font-size: 22px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
}

.yellow-red {
  background-color: rgba(221, 204, 0, 1);
}

.yellow-red img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: darken;
}

.yellow-red::after {
  background-color: rgba(221, 0, 0, 1);
  mix-blend-mode: lighten;
  color: white;
}

.green-blue {
  background-color: #00ff36
}

.green-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.green-blue::after {
  background-color: #23278a;
  mix-blend-mode: lighten;
  color: white;
}

.purple-green {
  background-color: #88169d;
}

.purple-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: lighten;
}

.purple-green::after {
  mix-blend-mode: hard-light;
  background: -webkit-gradient(linear, right bottom, left top, from(#75d775), to(#321a5b));
  background: -webkit-linear-gradient(bottom right, #75d775, #321a5b);
  background: linear-gradient(to top left, #75d775, #321a5b);
}

.Image {
  width: 100%;
}
.Boxes {
  background-color: #887600;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}

.Boxes-droppable {
  width: 100%;
  min-height: 120px;
  background-color: #984a00;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.BoxAsset {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-radius: 24px;
  font-size: 18px;
  font-weight: 400;
  margin: 10px;
  padding: 7px 10px 5px 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
